@font-face {
    font-family: Futura;
    src: url('./fonts/Futura\ PT.ttf');
}

@font-face {
    font-family: AdventPro;
    src: url('./fonts/AdventPro.ttf');
}

* {
    font-family: Futura;
    color: #596dff;
}

.main12345 {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin: 0 auto;
    /*height: 5000px;*/
    overflow: hidden;
}

h1 {
    margin: 0 auto;
}

.welcome-box {
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    background: white;
    width: 100%;
    height: 650px;
    display: flex;
    justify-content: center;
    z-index: -3;
    background-image: url('./../images/paper-bg.webp');
    background-size: cover;
}

.welcome-box::before {
    position: absolute;
    content: "";
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    background: white;width: 100%;
    height: 650px;
    display: flex;
    justify-content: center;
    z-index: -2;
    background-image: url('./../images/paper-bg.webp');
    background-size: cover;
    filter: blur(5px) brightness(90%);
}

.sub-welcome {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 60%;
    height: 270px;
    margin-top: 50px;
}

.sub1 {
    display: flex;
    background: linear-gradient(90deg, #ff0055, #ff7b00);
    width: fit-content;
    margin-top: 100px;
}

.welcome-sub-text {
    width: 60%;
    margin: 0;
    right: 0;
    margin-left: 10px;
}

.main-title {
    margin: 0;
    font-size: 55px;
    font-family: AdventPro;
    font-weight: 800;
}

.logo {
    filter: invert(1);
    margin-top: 5px;
    width: 260px;
    height: 60px;
}

.dog1 {
    position: absolute;
    width: 600px;
    bottom: -250px;
}

.content {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    width: 1200px;
}

.preview {
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    width: 1000px;
    overflow-x: auto;
    z-index: 16;
    padding-bottom: 20px;
    border-radius: 30px;
}

.preview::-webkit-scrollbar {
    background-color: rgb(255, 255, 255);
    border-radius: 30px;
    height: 30px;
}

.preview::-webkit-scrollbar-thumb {
    background: linear-gradient(90deg, #596dff, #1c32d1);
    border-radius: 30px;
}

.fade {
    position: absolute;
    width: 1000px;
    height: 320px;
    border-radius: 30px;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.1) 1%, transparent 10%, transparent 90%, rgba(0, 0, 0, 0.1) 99%);
    z-index: 18;
}

.prev-el {
    width: 250px;
    height: 300px;
    border-radius: 30px;
    background: transparent;
    margin-right: 20px;
    backdrop-filter: blur(5px);

    box-shadow: 15px 15px 2px rgba(0, 0, 0, 0.2);
}

.prev-el > h2 {
    width: 300px;
}

.prev-el > p {
    font-size: 25px;
}

.circl1 {
    position: absolute;
    border-radius: 25% 75% 82% 18% / 66% 58% 42% 34%;
    background: none;
    width: 400px;
    height: 250px;
    border: 1px solid #091ba1;
}

.circl2 {
    position: absolute;
    border-radius: 52% 48% 35% 65% / 66% 68% 32% 34%;
    background: none;
    width: 400px;
    height: 250px;
    border: 1px solid #1c32d1;
}

.circl3 {
    position: absolute;
    border-radius: 67% 33% 63% 37% / 40% 43% 57% 60% ;
    background: none;
    width: 1200px;
    height: 750px;
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    animation: rotate 10s linear infinite;
    z-index: -1;
    right: -350px;
    top: -200px;
    overflow: hidden;
    display: flex;
    justify-content: center;
}

@keyframes rotate {
    0% {
        border-radius: 67% 33% 63% 37% / 40% 43% 57% 60%
    }
    30% {
        border-radius: 25% 75% 82% 18% / 66% 58% 42% 34%
    }
    60% {
        border-radius: 52% 48% 35% 65% / 66% 68% 32% 34%
    }
    100% {
        border-radius: 67% 33% 63% 37% / 40% 43% 57% 60%
    }
}

.whyus {
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    width: 100%;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.whyus-title {
    margin-top: 30px;
    background: white;
    height: 90px;
    display: flex;
    width: 800px;
    justify-content: center;
    align-items: center;
}

.whyus-content {
    margin-top: 30px;
    height: 400px;
    display: flex;
    width: 70%;
    justify-content: space-between;
    overflow: hidden;
    -webkit-mask-image: linear-gradient(90deg, transparent 0%, white 3%, white 97%, transparent 100%);
}

.whyus1 {
    height: 100%;
    display: flex;
    justify-content: space-between;
    width: 100%;
    animation: slider 20s linear infinite;
}

@keyframes slider {
    0% {
        filter: none;
        transform: translateX(0%);
    }
    99% {
        filter: none;
        transform: translateX(-100%);
    }
    100% {
        filter: blur(10px);
        transform: translateX(0%);
    }
}

.whyus1::-webkit-scrollbar {
    height: 0;
}

.whyus2 {
    height: 100%;
    width: 790px;
    display: flex;
    flex-direction: column;
}

.whyus3 {
    display: flex;
    margin-bottom: 30px;
    justify-content: space-between;
}

.us-block1 {
    height: 100%;
    width: 385px;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 15px;
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
}

@keyframes gradient {
    0% {
        background-color: #ff0055;
    }
    50% {
        background-color: #ff7b00;
    }
    100% {
        background-color: #ff0055;
    }
}

.us-block2 {
    height: 185px;
    width: 365px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    padding-left: 15px;
}

.us-block3 {
    height: 185px;
    width: 775px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    padding-left: 15px;
}

.fas {
    width: 120px;
    height: 120px;
    font-size: 80px;
    color: rgb(255, 255, 255);
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
    position: relative;
}

.us-block1:hover {
    animation: gradient1 10s infinite linear;
    height: 355px;
    width: 335px;
    border: 15px solid white;
    background-image: linear-gradient(90deg, #ff7b00, #ff0055, #ff7b00);
    background-size: 200%;
    transition: background-image 2s;
}

.us-block2:hover {
    animation: gradient1 10s infinite linear;
    height: 155px;
    width: 335px;
    border: 15px solid white;
    background-image: linear-gradient(90deg, #ff7b00, #ff0055, #ff7b00);
    background-size: 200%;
    transition: background-image 2s;
}

.us-block3:hover {
    animation: gradient1 10s infinite linear;
    height: 355px;
    width: 745px;
    border: 15px solid white;
    background-image: linear-gradient(90deg, #ff7b00, #ff0055, #ff7b00);
    background-size: 200%;
    transition: background-image 2s;
}

.ublock:hover .fas {
    /*
    background: -webkit-linear-gradient(90deg, #ff7b00, #ff0055);
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    */
    background: transparent;
}

.ublock:hover .ublock-text {
    background: white;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

@keyframes gradient1 {
    0% {
        background-position: left;
    }
    50% {
        background-position: right;
    }
    100% {
        background-position: left;
    }
}

.ublock-text {
    font-weight: 700;
    font-size: 25px;
    text-align: center;
    margin-top: 5px;
    margin-left: 15px;
    margin-right: 15px;
    
    background: linear-gradient(90deg, #ff7b00, #ff0055);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.ub1 {
    width: 330px;
}

.ub2 {
    width: 200px;
}

.ub3 {
    width: 600px;
}

.stats {
    width: 100%;
    height: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('./../images/paper-bg.webp');
    z-index: 3;
    overflow: hidden;
}

.stats::before {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('./../images/paper-bg.webp');
    filter: blur(5px) brightness(90%);
    z-index: 2;
}

.stat-list {
    width: calc(100% + 50%);
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 50px;
}

.stat-line {
    width: fit-content;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    align-self: center;
}

.stat {
    width: 200px;
    height: 300px;
    background: #ffffff;
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.fas2 {
    width: 120px;
    height: 120px;
    font-size: 80px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
    background: -webkit-linear-gradient(0deg, #ff7b00, #ff0055);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.fa-store-alt, .fa-signal {
    font-size: 68px;
}

.fa-hand-holding-usd {
    font-size: 75px;
}

.stat-header {
    font-size: 30px;
    z-index: 7;
}

.stat-quantity {
    position: absolute;
    width: 100%;
    font-size: 60px;
    bottom: 0;
    text-align: center;
    z-index: 5;
}

.stat-quantity::before {
    position: absolute;
    content: attr(data-content);
    font-weight: 900;
    font-size: 200px;
    z-index: -3;
    bottom: -40px;
    background: -webkit-linear-gradient(#ffffff 0%, #ffffff36 70%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.running-bg-ph {
    display: flex;
    flex-direction: column;
    align-self: center;
    position: absolute;
    z-index: -2;
}

.bgph1 {
    transform: rotate(-30deg);
    top: -100px;
    left: -200px;
    z-index: -1;
}

.bgph2 {
    transform: rotate(-75deg);
    top: 100px;
    left: -300px;
}

.bgph3 {
    transform: rotate(25deg);
    top: 100px;
    right: 100px;
}

.bgph4 {
    transform: rotate(60deg);
    top: 100px;
    right: -200px;
    z-index: -3;
}

.bgph5 {
    transform: rotate(-10deg);
    top: 500px;
    right: 100px;
}

.running-bg {
    position: relative;
    display: flex;
    height: fit-content;
    width: 2000px;
    overflow: hidden;
    background-image: linear-gradient(90deg, #ff7b00, #ff0055, #ff7b00);
    background-size: 200%;
    align-self: center;
    animation: gradshift 10s linear infinite;
}

@keyframes gradshift {
    0% {
        background-position: left;
    }
    50% {
        background-position: right;
    }
    100% {
        background-position: left;
    }
}

.stat-line-bg {
    white-space: nowrap;
}

.statbg1 {
    animation: animate var(--t) linear infinite;
    animation-delay: calc(var(--t) / -1);
}

.statbg2 {
    animation: animate2 var(--t) linear infinite;
    animation-delay: calc(var(--t) / -2);
}

.statbg3 {
    animation: animate3 var(--t) linear infinite;
    animation-delay: calc(var(--t) / -1);
}

.statbg4 {
    animation: animate4 var(--t) linear infinite;
    animation-delay: calc(var(--t) / -2);
}

.ittaboo-logo2 {
    margin: 10px;
    font-family: AdventPro;
    font-size: 50px;
    font-weight: 700;
    font-stretch: expanded;
    color: transparent;
    background-image: url('./../images/logo.png');
    background-size: 350px;
    display: inline-flex;
    padding: 10px;
    border-radius: 5px;
    height: fit-content;
    width: 350px;
    transition: 0.5s;
}

.ittaboo-logo2:hover {
    cursor: pointer;
}

@keyframes animate {
    0% {
        transform: translateX(100%);
    }

    100% {
        transform: translateX(-100%);
    }
}

@keyframes animate2 {
    0% {
        transform: translateX(0%);
    }

    100% {
        transform: translateX(-200%);
    }
}

@keyframes animate3 {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(100%);
    }
}

@keyframes animate4 {
    0% {
        transform: translateX(-200%);
    }

    100% {
        transform: translateX(0%);
    }
}

