    width: 95%;
    justify-content: space-between;
}

.lostxx {
    width: 31%;
    height: 200px;
    background-image: linear-gradient(90deg, #ff7b00, #ff0055);
    position: relative;
    background-image: url('./../images/dog-bg.jpg');
    background-size: 121%;
    background-position: center;
}

.lostxx1 {
    color: white;
    font-size: 35px;
    font-weight: 600;
    background-image: linear-gradient(180deg, #000000 30%, #ff005500 100%);
}

.lostxx2 {
    position: absolute;
    color: white;
    font-size: 20px;
    bottom: 0;
    background-image: linear-gradient(180deg, #00000000 30%, #ff0055 100%);
}

.xx1 {
    animation: gradchange 10s linear infinite;
}

.xx2 {
    animation: gradchange2 10s linear infinite;
}

@keyframes gradchange {
    0% {
        background-image: linear-gradient(180deg, #00000000 30%, #ff0055 100%);
    }
    50% {
        background-image: linear-gradient(180deg, #00000000 30%, #ff7b00 100%);
    }
    100% {
        background-image: linear-gradient(180deg, #00000000 30%, #ff0055 100%);
    }
}

@keyframes gradchange2 {
    0% {
        background-image: linear-gradient(180deg, #00000000 30%, #ff7b00 100%);
    }
    50% {
        background-image: linear-gradient(180deg, #00000000 30%, #ff0055 100%);
    }
    100% {
        background-image: linear-gradient(180deg, #00000000 30%, #ff7b00 100%);
    }
}

